.About{
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
    position:relative;
}

a:visited{
  color:  #61dafb;
}

span{
  color: rgb(241, 62, 62)
}

.About h1 NavLink:hover{
  color: white;
}


.Navigation{
    background-color: #282c34;
    color:  #61dafb;
    text-align: left;
    left: 400px;
  }
  
  .Navigation NavLink{
    display: block;
  }
  
  .Navigation a:visited{
    background-color: #282c34;
    color:  #61dafb;
  }
  .Navigation a:link {
    color: #61dafb;
  }
  
  .Navigation a:hover{
    color: white;
  }
  
.home{
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
    position:relative;
  }

  span svg{
    fill: white;
    padding-left: 0.5em;
    border: #61dafb solid 2px;
    padding-right: 0.5em;
    margin-left: 0.5em;
    border-radius: 25px;
}
.Projects{
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
    position:relative;
  }

  h1 a:visited{
    color:  #61dafb;
  }

  h1 a:link{
    color:  #61dafb;
  }

  span{
      color: rgb(241, 62, 62)
  }

  .borderBox{
    border: solid grey 1px;
    /* position:absolute; */
    width:60%;
    text-align: center;
  }

  a img{
      height: 40px;
  }

  a:hover{
    color: white;
  }

  .projects{
    position: relative;
    height: 100%;
    width: 100%;
  }
.Error{
